import Local from './local';
import Remote from './remote';
import Connection from 'Wrappers/Connection';
import Fluent from '../../Fluent';

export default Fluent.compose({
  properties: {
    getFrom: 'remote-local',
    modelName: 'baseModel',
    formioPath: undefined
  },
  methods: {
    getModelName () {
      return this.modelName;
    },
    /**
     * [remote description]
     * @return {[type]} [description]
     */
    remote () {
      this.getFrom = 'remote';
      return this;
    },
    /**
     * [local description]
     * @return {[type]} [description]
     */
    local () {
      this.getFrom = 'local';
      return this;
    },
    /**
     *
     */
    merged () {
      this.getFrom = 'remote-local';
      return this;
    },
    /**
     * [find description]
     * @param  {[type]} filter [description]
     * @return {[type]}        [description]
     */
    async find ({ filter, limit = undefined, select, pagination, form, populate, reviewer } = {}) {
      switch (this.getFrom) {
        case 'local':
          return Local.find({
            modelName: this.modelName,
            filter,
            limit,
            select,
            pagination,
            form
          });
          break;
        case 'remote':
          if (this.formioPath === 'custom') {
            return this.rFind({
              formPath: this.formioPath,
              filter,
              limit,
              select,
              pagination,
              populate,
              form
            });
          }
          let isOnline = await Connection.isOnline();

          return isOnline ?
            Remote.find({
              formPath: form || this.formioPath,
              filter,
              limit,
              select,
              pagination,
              populate
            }) :
            [];
          break;
        case 'remote-local':
          let local = await Local.find({
            modelName: this.modelName,
            filter,
            limit,
            select,
            pagination,
            form
          });
          let remote;

          if (this.formioPath === 'custom') {
            let isOnline = await Connection.isOnline();

            remote = isOnline ?
              await this.rFind({
                formPath: this.formioPath,
                filter,
                limit,
                select,
                pagination,
                populate
              }) :
              [];
          } else {
            let isOnline = await Connection.isOnline();

            remote = isOnline ?
              await Remote.find({
                formPath: form || this.formioPath,
                filter,
                limit,
                select,
                pagination,
                populate
              }) :
              [];
          }

          // We need to include a logic here to check Which submission to keep
          // It could be the offline synced submission, the merge of the two
          // Or just keep the online submission
          let localOnline = local.reduce((reducer, s) => {
            if (s.data && s.data._id && s.data._id.indexOf('_local') === -1) {
              reducer.push(s.data._id);
            }
            return reducer;
          }, []);

          remote = remote.filter((s) => {
            return !localOnline.includes(s._id);
          });

          return remote.concat(local);
          break;
      }
    },
    /**
     * [findOne description]
     * @param  {[type]} filter [description]
     * @return {[type]}        [description]
     */
    async findOne (filter) {
      switch (this.getFrom) {
        case 'local':
          return Local.findOne({ modelName: this.modelName, filter: filter });
          break;
        case 'remote':
          return;
          break;
        case 'remote-local':
          return;
          break;
      }
    },
    /**
     * [remove description]
     * @param  {[type]} document [description]
     * @return {[type]}          [description]
     */
    async remove (document, id, formPath) {
      switch (this.getFrom) {
        case 'local':
          return Local.remove({
            modelName: this.modelName,
            document: document
          });
          break;
        case 'remote':
          let isOnline = await Connection.isOnline();

          return isOnline ?
            Remote.remove({
              formPath,
              id
            }) :
            undefined;
          return;
          break;
        case 'remote-local':
          return;
          break;
      }
    },
    /**
     * [remove description]
     * @param  {[type]} document [description]
     * @return {[type]}          [description]
     */
    async softDelete ({ id, formPath }) {
      switch (this.getFrom) {
        case 'local':
          break;
        case 'remote':
          let isOnline = await Connection.isOnline();

          return isOnline ?
            Remote.softDelete({
              formPath,
              id
            }) :
            undefined;
          return;
          break;
        case 'remote-local':
          return;
          break;
      }
    },
    /**
     * [insert description]
     * @param  {[type]} element [description]
     * @return {[type]}         [description]
     */
    async insert (element, formPath) {
      switch (this.getFrom) {
        case 'local':
          return Local.insert({ modelName: this.modelName, element: element });
          break;
        case 'remote':
          return Remote.insert({
            formPath: this.formioPath || formPath,
            element: element
          });
          break;
        case 'remote-local':
          return;
          break;
      }
    },
    /**
     * [update description]
     * @param  {[type]} document [description]
     * @return {[type]}          [description]
     */
    async update (document, formPath) {
      switch (this.getFrom) {
        case 'local':
          return Local.update({
            modelName: this.modelName,
            document: document
          });
          break;
        case 'remote':
          return Remote.update({
            formPath: this.formioPath || formPath,
            document: document
          });
          break;
        case 'remote-local':
          return;
          break;
      }
    },

    async clear () {
      switch (this.getFrom) {
        case 'local':
          return Local.clear({
            modelName: this.modelName
          });
          break;
        case 'remote':
          break;
        case 'remote-local':
          return;
          break;
      }
    },
    /**
     *
     * @param {*} document
     */
    async updateOrCreate (document) {
      switch (this.getFrom) {
        case 'local':
          return Local.updateOrCreate({
            modelName: this.modelName,
            document: document
          });
          break;
        case 'remote':
          return;
          break;
        case 'remote-local':
          return;
          break;
      }
    },

    async findAndRemove (filter) {
      switch (this.getFrom) {
        case 'local':
          return Local.findAndRemove({
            modelName: this.modelName,
            filter: filter
          });
          break;
        case 'remote':
          return;
          break;
        case 'remote-local':
          return;
          break;
      }
    }
  }
}).compose(Fluent.privatize);
